<template>
    <div class="star" :class="starType">
        <span v-for="itemClass in itemClasses" :class="itemClass" class="star-item"></span>
    </div>
</template>

<script>
    const LENGTH = 5;
    const CLS_ON = "on";
    const CLS_HALF = "half";
    const CLS_OFF = "off";

    export default{
        props : {
            size : {
                type : Number
            },
            score : {
                type : Number
            }
        },
        data(){
            return {}
        },
        computed : {
            starType(){
                return "star-" + this.size;
            },
            itemClasses(){
                let result = [];

                let score = Math.floor( this.score * 2 ) / 2;
                let hasDecimal = score % 1 !== 0;
                let integer = Math.floor(score);

                for( let i=0;i<integer&&i<LENGTH;i++ ){
                    result.push( CLS_ON );
                }
                if( hasDecimal ){
                    result.push( CLS_HALF );
                }
                while( result.length < LENGTH ){
                    result.push( CLS_OFF );
                }

                return result;
            }
        }
    };
</script>

<style lang="less">
    @import "../../common/css/base.less";
    .star{
        font-size: 0;
        .star-item{
            display:inline-block;
            background-repeat: none;
        }
        
        .star-item:last-child{
            margin-right: 0!important;
        }
    }
    .star-48{
        .star-item{
            width:20px;
            height:20px;
            margin-right:22px;
            background-size: 20px 20px;
        }
        .on{
            .bgimage("star48_on");
        }
        .half{
            .bgimage("star48_half");
        }
        .off{
            .bgimage("star48_off");
        }
    }
    .star-36{
        .star-item{
            width:15px;
            height:15px;
            margin-right:6px;
            background-size: 15px 15px;
        }
        .on{
            .bgimage("star36_on");
        }
        .half{
            .bgimage("star36_half");
        }
        .off{
            .bgimage("star36_off");
        }
    }
    .star-24{
        .star-item{
            width:10px;
            height:10px;
            margin-right:3px;
            background-size: 10px 10px;
        }
        .on{
            .bgimage("star24_on");
        }
        .half{
            .bgimage("star24_half");
        }
        .off{
            .bgimage("star24_off");
        }
    }
</style>


