<template>
    <div class="rating" ref="rating">
        <div class="ratings-content">
            <div class="overview">
                <div class="overview-left">
                    <h1 class="score">{{seller.score}}</h1>
                    <div class="title">综合评分</div>
                    <div class="rank">高于周边商家{{seller.rankRate}}%</div>
                </div>
                <div class="overview-right">
                    <div class="score-wrapper">
                        <span class="title">服务态度</span>
                        <star :size="36" :score="seller.serviceScore"></star>
                        <span class="score">{{seller.serviceScore}}</span>
                    </div>
                    <div class="score-wrapper">
                        <span class="title">商品评分</span>
                        <star :size="36" :score="seller.foodScore"></star>
                        <span class="score">{{seller.foodScore}}</span>
                    </div>
                    <div class="delivery-wrapper">
                        <span class="title">送达时间</span>
                        <span class="delivery">{{seller.deliveryTime}}分钟</span>
                    </div>
                </div>
            </div>
            <split></split>
            <ratingselect 
                :selected-type="selectedType"
                :only-content-p="onlyContentP"
                :desc="desc"
                :ratings="ratings">
            </ratingselect>
            <div class="rating-wrapper">
                <ul>
                    <li v-for="(rating,index) in ratings" class="rating-item" v-show="needShow(rating.rateType,rating.text)">
                        <div class="avatar">
                            <img width="28" height="28" :src="rating.avatar" />
                        </div>
                        <div class="content">
                            <h1 class="name">{{rating.username}}</h1>
                            <div class="star-wrapper">
                                <star :size="24" :score="rating.score"></star>
                                <span class="delivery" v-show="rating.deliveryTime">
                                    {{rating.deliveryTime}}
                                </span>
                            </div>
                            <p class="text" :class="{'mb':rating.recommend && rating.recommend.length}">{{rating.text}}</p>
                            <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                                <span class="icon-thumb_up"></span>
                                <span v-for="(item,index) in rating.recommend" class="item">
                                    {{item}}
                                </span>
                            </div>
                            <div class="time">
                                {{rating.rateTime | formatDate}}
                            </div>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    const POSITIVE = 0;
    const NEGATIVE = 1;
    const ALL = 2;

    import star from '../star/star.vue';
    import split from '../split/split';
    import ratingselect from '../ratingselect/ratingselect';
    import Bus from '../../common/js/eventBus';
    import BScroll from 'better-scroll';
    import {formatDate} from '../../common/js/date.js'
    import {data} from '../../common/js/data.js'
    export default{
        props : {
            seller : {
                type : Object
            }
        },
        data(){
            return {
                ratings : [],
                selectedType : ALL,
                onlyContentP : true,
                desc : {
                    all:'全部',
                    positive : '推荐',
                    negative : '吐槽'
                }
            }
        },
        filters : {
            formatDate(time){
                let date = new Date(time);
                return formatDate(date,"yyyy-MM-dd");
            }
        },
        created(){
            this.ratings = data.ratings
            this.$nextTick(() => {
                if( !this.scroll ){
                    this.scroll = new BScroll(this.$refs.rating,{
                        click:true
                    })
                }else{
                    this.scroll.refresh();
                }
            })
            
//          this.$http.get("/api/ratings").then(
//              function (res) {
//                  // console.log(res)
//                  // 处理成功的结果
//                  var result = res.body;
//                  this.ratings = result.data||{};
//                  this.$nextTick(() => {
//                      if( !this.scroll ){
//                          this.scroll = new BScroll(this.$refs.rating,{
//                              click:true
//                          })
//                      }else{
//                          this.scroll.refresh();
//                      }
//                  })
//              },function (res) {
//              // 处理失败的结果
//              }
//          );
            var _this = this;
            Bus.$on('ratingtype.select', type => {
                this.selectedType = type;
                // console.log(this.selectedType,"selectTypeppppp");
                this.$nextTick(() => {
                    _this.scroll.refresh();
                });
            });
            Bus.$on('ratingtype.only', only => {
                this.onlyContentP = only;
                // console.log(this.onlyContentP+"onlyContentppppppp");
                this.$nextTick(() => {
                    _this.scroll.refresh();
                });
            });
        },
        components:{
            star,
            split,
            ratingselect
        },
        methods:{
            needShow(type,text){
                if( this.onlyContentP && !text ){
                    return false;
                }
                if( this.selectedType === ALL ){
                    return true;
                }else{
                    return type === this.selectedType;
                }
            }
        }
    };
</script>

<style lang="less">
    @import "../../common/css/base.less";
    .rating{
        position:absolute;
        width:100%;
        top:174px;
        bottom:0;
        left:0;
        overflow:hidden;
        .overview{
            display:flex;
            padding:18px 0;
            .overview-left{
                flex:0 0 137px;
                padding: 6px 0;
                width:137px;
                border-right:1px solid rgba(7,17,27,0.1);
                text-align: center;
                @media only screen and (max-width:320px){
                    flex:0 0 120px;
                    width:120px;
                }
                .score{
                    margin-bottom: 6px;
                    line-height: 28px;
                    font-size: 24px;
                    color:rgb(255,153,0);
                }
                .title{
                    margin-bottom: 8px;
                    line-height: 12px;
                    font-size: 12px;
                    color:rgb(7,17,27);
                }
                .rank{
                    line-height:10px;
                    font-size: 10px;
                    color:rgb(147,153,159);
                }
            }
            .overview-right{
                flex:1;
                padding:6px 0 6px 24px;
                @media only screen and (max-width:320px){
                    padding:6px 0 6px 6px;
                }
                .score-wrapper{
                    margin-bottom: 8px;
                    font-size: 0;
                    .title{
                        display:inline-block;
                        line-height: 18px;
                        vertical-align: top;
                        font-size: 12px;
                        color:rgb(7,17,27);
                        margin-right: 12px;
                    }
                    .star{
                        display:inline-block;
                        vertical-align: top;
                        margin-right: 12px;
                    }
                    .score{
                        display:inline-block;
                        line-height: 18px;
                        vertical-align: top;
                        font-size: 12px;
                        color:rgb(255,153,0);
                    }
                }
                .delivery-wrapper{
                    font-size: 0;
                    .title{
                        line-height: 18px;
                        font-size: 12px;
                        color:rgb(7,17,27);
                        margin-right: 12px;
                    }
                    .delivery{
                        font-size: 12px;
                        color:rgb(147,153,159);
                    }
                }
            }
        }
        .rating-wrapper{
            padding:0 18px;
            .rating-item{
                display:flex;
                padding:18px 0;
                position:relative;
                .border-1px(rgba(7,17,27,0.1));
                .avatar{
                    flex:0 0 28px;
                    width:28px;
                    margin-right: 12px;
                    img{
                        border-radius: 50%;
                    }
                }
                .content{
                    flex:1;
                    position:relative;
                    .name{
                        margin-bottom: 4px;
                        line-height: 12px;
                        font-size: 10px;
                        color:rgb(7,17,27);
                    }
                    .star-wrapper{
                        margin-bottom: 6px;
                        font-size: 0;
                        .star{
                            display:inline-block;
                            vertical-align: top;
                            margin-right: 6px;
                        }
                        .delivery{
                            display:inline-block;
                            vertical-align: top;
                            line-height: 12px;
                            font-size: 10px;
                            color:rgb(147,153,159);
                        }
                    }
                    .text{
                        line-height: 18px;
                        color:rgb(7,17,27);
                        font-size: 12px;
                        &.mb{
                            margin-bottom: 8px;
                        }
                    }
                    .recommend{
                        line-height: 16px;
                        font-size: 0;
                        .icon-thumb_up,.item{
                            display:inline-block;
                            margin:0 8px 4px 0;
                            font-size: 9px;
                        }
                        .icon-thumb_up{
                            color:rgb(0,160,220);
                        }
                        .item{
                            padding:0 6px;
                            border:1px solid rgba(7,17,27,0.1);
                            border-radius: 1px;
                            color:rgb(147,153,159);
                            background:#fff;
                        }
                    }
                    .time{
                        position:absolute;
                        top:0;
                        right:0;
                        line-height: 12px;
                        color:rgb(147,153,159);
                    }
                }
            }
        }
    }
</style>


